export default {
  data: function() {
    return {
      slugField: {
        maxLength: 50,
        defaultValue: "",
        hasDefault: false
      },
    };
  },
  methods: {
    getData: function() {
      return this.slugField;
    },
    isSlug: function(val) {
      return new RegExp(`[a-zA-Z0-9_-]`).test(val);
    },
  },
};
