export default {
  data: function() {
    return {
      emailField: {
        maxLength: 254,
        defaultValue: "",
        hasDefault: false,
      },
    };
  },
  methods: {
    getData: function() {
      return this.emailField;
    },
    isEmail: function(val) {
      return /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(val);
    },
  },
};
