export default {
  data: function() {
    return {
      urlField: {
        maxLength: 200,
        defaultValue: "",
        hasDefault: false,
      },
    };
  },
  methods: {
    getData: function() {
      return this.urlField;
    },
    isUrl: function(val) {
      return /^(ftp|http|https):\/\/[^ "]+$/.test(val);
    },
  },
};
